.container__contactMe{margin-bottom:60px}.container__contactMe .container__card__contactMe{display:flex;align-items:center;margin-top:2rem}.container__contactMe .container__card__contactMe span{font-size:2rem}.container__contactMe .container__card__contactMe .contactMe__info{margin-left:1rem}.container__contactMe .container__card__contactMe .contactMe__info p{margin-bottom:0px}.container__contactMe .container__card__contactMe a{text-decoration:none;font-size:.875rem;display:flex;align-items:center;border:1px solid var(--var-primary-gray);padding:1rem;border-radius:4px;transition:all .3s}.container__contactMe .container__card__contactMe a .contactMe__info p::after{content:" ";transform:translateX(3px);transition:all .3s ease-in-out;opacity:0}.container__contactMe .container__card__contactMe a:hover{cursor:pointer;transform:scale(1.05);transition:all .3s}.container__contactMe .container__card__contactMe a:hover .contactMe__info p::after{transform:translateX(7px);opacity:1}@media(min-width: 768px){.container__contactMe .container__form{display:grid;grid-template-columns:repeat(2, 1fr)}}